<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-button size="small" type="primary" @click="mgrForm"></el-button>
      </el-row>
      <div class="space"></div>
      <!--  -->
      <el-form :inline="true" :model="form">
        <el-form-item label="">
          <el-input size="small" v-model="form.keywords" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="form.status" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option v-for="item in statusList" :key="item.code" :label="item.name" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <div></div>
        <el-form-item>
          <el-button size="small" type="primary" @click="list"></el-button>
          <el-button size="small" @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="space"></div>
    <el-card class="box-card">
      <!--  -->
      <el-table :data="tableList" style="width: 100%">
        <el-table-column label="" type="index" align="center">
          <template slot-scope="scope">
            {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column prop="roleName" label="" align="center"></el-table-column>
        <el-table-column prop="status" label="" align="center">
          <template slot-scope="scope">
            {{scope.row.status==1?"":""}}
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="160">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status==1" size="mini" @click="mgrForm(scope.$index, scope.row)"></el-button>
            <el-button v-if="scope.row.status==1" size="mini" @click="handleDelete(scope.$index, scope.row)" type="danger"></el-button>
            <el-button v-if="scope.row.status==0" size="mini" @click="modifyData(scope.row.id, 1)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="space"></div>
      <!--  -->
      <el-pagination background layout="total, prev, pager, next, jumper"
        @size-change="sizeChange" @current-change="currentChange"
        :current-page="form.pageNo" :page-size="form.pageSize"
        :total="pagination.total">
      </el-pagination>
    </el-card>

    <el-dialog title="" width="550px" center :visible.sync="roleForm.show">
      <el-form :model="roleForm" class="form">
        <el-form-item label="">
          <el-input size="small" v-model="roleForm.roleName" placeholder="" :disabled="roleForm.id?true:false"></el-input>
        </el-form-item>
        <el-form-item>
          <div class="label"></div>
          <div class="menu-tree">
            <el-tree ref="menuTree"
              :data="menus.datas"
              show-checkbox
              node-key="id"
              :default-expanded-keys="menus.defaultExpanded"
              :default-checked-keys="menus.defaultChecked"
              :props="menus.props">
            </el-tree>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="roleForm.show=false"> </el-button>
        <el-button size="small" type="primary" @click="roleFormOk"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      statusList: [],
      form: {
        pageNo: 1,
        pageSize: 10,
        keywords: '', // 
        status: '', // 
      },
      pagination: { // 
        total: 0
      },
      tableList: [], // 
      roleForm: { // 
        show: false, // 
        id: '', // id
        roleCode: '', // 
        roleName: '', // 
        menus: '' // menu1,menu2,menu3
      },
      menus: {
        datas: [], // 
        defaultExpanded: [], // 
        defaultChecked: [], // 
        props: {
          children: 'children',
          label: 'label'
        }
      }
    }
  },
  created() {
    this.getStatus();
    this.list();
  },
  methods: {
    currentChange(pageNo) {
      // pageNo
      this.list(event, pageNo);
    },
    sizeChange(pageSize) {
      // pageSize
      this.list(event, null, pageSize);
    },
    // 
    getStatus() {
      this.$request.post('/api/saotx/dim/dataStatus', {}, false, (res)=>{
        if (res.ret == '200000') {
          this.statusList = res.data||[];
        }
      });
    },
    // 
    resetForm() {
      this.form = {
        pageNo: 1,
        pageSize: 10,
        keywords: '', // 
        status: '' // 
      }
      this.list();
    },
    // 
    list(_event, pageNo, pageSize) {
      if(pageNo) {
        this.form.pageNo = pageNo;
      } else {
        this.form.pageNo = 1;
      }
      if(pageSize) {
        this.form.pageSize = pageSize;
      } else {
        this.form.pageSize = 10;
      }
      this.$request.post('/api/saotx/role/list', this.form, true, (res)=>{
        if (res.ret == '200000') {
          this.tableList = res.data.list || [];
          this.initPagination(res.data.page||{});
        }
      });
    },
    // page = {"pageCount":, "count":}
    initPagination(page) {
      if(page) {
        this.pagination.total = page.count;
      }
    },
    // confirm
    handleDelete(index, row) {
      this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(() => {
          this.modifyData(row.id, 0);// 
        }).catch(() => {/**  */});
    },
    // status=1status=0
    modifyData(id, status) {
      let params = {id:id, status:status}
      this.$request.post('/api/saotx/role/mstatus', params, true, (res)=>{
        if (res.ret == '200000') {
          this.list();
          this.$message({type: 'success', message: '!'});
        } else {
          this.$message.error(res.message);
        }
      });
    },
    roleFormInit() {
      this.roleForm = {
        show: false,
        id: '', // id
        roleCode: '', // 
        roleName: '', // 
        menus: [] // 
      };
      this.menus = {
        datas: [], // 
        defaultExpanded: [], // 
        defaultChecked: [], // 
        props: {
          children: 'children',
          label: 'label'
        }
      };
    },
    // 
    mgrForm(index, row) {
      if(row&&row.id) {// 
        this.roleForm.id = row.id;
        this.roleForm.roleCode = row.roleCode;
        this.roleForm.roleName = row.roleName;
      } else {// 
        this.roleFormInit();
      }
      this.$request.post('/api/saotx/role/queryByCode', this.roleForm, true, (res)=>{
        if (res.ret == '200000') {
          this.roleForm.id = res.data.id||'';
          this.roleForm.roleCode = res.data.roleCode||'';
          this.roleForm.roleName = res.data.roleName||'';
          let menuNode = res.data.nodes||[]; // 
          let treeNodes = [];
          for(let i=0;i<menuNode.length;i++) {
            let menu = menuNode[i];
            if(!menu.pId) { // 
              let node = {id:menu.id, label:menu.name, checked:menu.checked};
              this.recursiveMenu(menuNode, node);
              treeNodes.push(node);
            }
          }
          this.menus.datas = treeNodes;
          this.roleForm.show = true;
        } else {
          this.$message.error(res.message);
        }
      });
    },
    // 
    recursiveMenu(menuNode, currentNode) {
      let children = [];
      for(let i=0;i<menuNode.length;i++) {
        let menu = menuNode[i];
        if(currentNode.id==menu.pId) { // currentNode
          let node = {id:menu.id, label:menu.name, checked:menu.checked};
          this.recursiveMenu(menuNode, node);
          children.push(node);
        }
      }
      currentNode.children = children;
      if(currentNode.children.length==0&&currentNode.checked) {// 
        this.menus.defaultChecked.push(currentNode.id);
      }
    },
    // 
    roleFormOk() {
      if(!this.roleForm.roleName) {
        this.$message.error('');
        return false;
      }
      let checkedMenus = this.$refs.menuTree.getHalfCheckedKeys()||[];
      let checkedKeys = this.$refs.menuTree.getCheckedKeys()||[];
      if(checkedMenus&&checkedMenus.length>0) {
        checkedMenus = checkedMenus.concat(checkedKeys);
      }
      this.roleForm.menus = checkedMenus.join(',')||'';
      this.$request.post('/api/saotx/role/saveOrModify', this.roleForm, true, (res)=>{
        if (res.ret == '200000') {
          this.$message({type: 'success', message: '!'});
          this.roleForm.show = false;
          this.list();
        } else {
          this.$message.error(res.message);
        }
      });
    }
  }
}
</script>

<style scoped>
  .space {position:relative;width:100%;height:20px;}
  .dialog-footer {
    clear: both;
  }
  .el-input, .el-select {
    width: 200px;
  }
  .form {
    width: 100%;
    overflow: hidden;
  }
  .form .label {
    float: left;
    width: 70px;
  }
  .form .menu-tree {
    float: left;
    width: 400px;
    height: 240px;
    overflow: auto;
  }
  .form .el-input {
    width: 400px;
  }
</style>



