<template>
  <div>
    <el-card class="box-card">
        <!--  -->
        <el-form :inline="true" :model="form" class="demo-form-inline">
            <el-form-item label="">
              <el-select size="small" v-model="form.operType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="(label, value) in types" :key="value" :label="label" :value="value"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
                <div class="block">
                    <el-date-picker  size="small"
                        v-model="form.time"
                        type="datetimerange"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        :default-time="['00:00:00', '23:59:59']"
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </div>
            </el-form-item>
            <el-form-item label="">
                <el-input size="small" v-model="form.userName"></el-input>
            </el-form-item>
            <div></div>
            <el-form-item>
                <el-button size="small" type="primary" @click="list"></el-button>
                <el-button size="small" @click="resetForm"></el-button>
            </el-form-item>
        </el-form>
      </el-card>
      <div class="space"></div>
      <el-card class="box-card">
        <!--  -->
        <el-table v-loading="loading" :data="tableList" style="width: 100%">
          <el-table-column label="" type="index" align="center">
            <template slot-scope="scope">
              {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column prop="operUsername" label="" align="center" width="140"></el-table-column>
          <el-table-column label="" align="center" width="160">
            <template slot-scope="scope">
              {{new Date(scope.row.operTime).Format("yyyy-MM-dd hh:mm:ss")}}
            </template>
          </el-table-column>
          <el-table-column prop="clientIp" label="IP" align="center" width="130"></el-table-column>
          <el-table-column prop="operName" label="" align="center" width="80"></el-table-column>
          <el-table-column prop="operBusiness" label="" align="center" width="120"></el-table-column>
          <el-table-column prop="operContent" label="" align="center"></el-table-column>
        </el-table>
        <div class="space"></div>
        <!--  -->
        <el-pagination background layout="total, prev, pager, next, jumper"
            @size-change="sizeChange" @current-change="currentChange"
            :current-page="form.pageNo" :page-size="form.pageSize"
            :total="pagination.total">
        </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
        loading: false, // 
        types: [],
        pickerOptions: {
            disabledDate(time) {
                return time.getTime() > Date.now();
            },
            // selectDate = { maxDate, minDate }
            onPick(selectDate) {
                //console.log(selectDate.minDate);
                //console.log(selectDate.maxDate);
            }
        },
        form: { // 
            pageNo: 1,
            pageSize: 10, 
            operType: "", // 
            time: [], // 
            stime: "", // -- ::
            etime: "", // -- ::
            userName: "" // 
        },
        tableList: [],
        pagination: { // 
            total: 0
        }
    }
  },
  created() {
      let _now = new Date();
      this.form.time = [_now.Format("yyyy-MM-dd")+" 00:00:00", _now];
      this.getTypes();
      this.list();
  },
  methods: {
    currentChange(pageNo) {
      // pageNo
      this.list(event, pageNo);
    },
    sizeChange(pageSize) {
      // pageSize
      this.list(event, null, pageSize);
    },
    // 
    getTypes() {
      this.$request.post('/api/saotx/log/types', {pageSize:-1}, true, (res)=>{
        if (res.ret == '200000') {
          this.types = res.data||{};
        }
      });
    },
    // 
    resetForm() {
      this.form = {
        pageNo: 1,
        pageSize: 10, 
        operType: "", // 
        stime: "", // -- ::
        etime: "", // -- ::
        userName: "" // 
      };
      let _now = new Date();
      this.form.time = [_now.Format("yyyy-MM-dd")+" 00:00:00", _now];
      this.list();
    },
    // page = {"pageCount":, "count":}
    initPagination(page) {
      if(page) {
        this.pagination.total = page.count;
      }
    },
    // 
    list(_event, pageNo, pageSize) {
      if(pageNo) {
        this.form.pageNo = pageNo;
      } else {
        this.form.pageNo = 1;
      }
      if(pageSize) {
        this.form.pageSize = pageSize;
      } else {
        this.form.pageSize = 10;
      }

      this.loading = true;
      if(this.form.time&&this.form.time.length==2) {
          this.form.stime = new Date(this.form.time[0]).Format("yyyy-MM-dd hh:mm:ss");
          this.form.etime = new Date(this.form.time[1]).Format("yyyy-MM-dd hh:mm:ss");
      } else {
          this.form.stime = this.form.etime = "";
      }
      this.$request.post('/api/saotx/log/list', this.form, true, (res)=>{
          this.loading = false;
          if (res.ret == '200000') {
              this.tableList = res.data.list || [];
              this.initPagination(res.data.page||{});
          } else {
              this.$message.error(res.message);
          }
      });
    }
  }
}
</script>

<style scoped>
  .space {position:relative;width:100%;height:20px;}
  .el-input, .el-select {
    width: 200px;
  }
</style>



